import moment from "moment";

export default {
  filters: {
    formatDate: function(value) {
      return moment.unix(value).calendar(null, {
        sameDay: "[Сегодня], LT",
        nextDay: "[Завтра], LT",
        nextWeek: "DD MMM YYYY, HH:mm",
        lastDay: "[Вчера], LT",
        lastWeek: "DD MMM YYYY, HH:mm",
        sameElse: "DD MMM YYYY, HH:mm"
      });
    },
    fdate: function(value) {
      return moment.unix(value).format("DD.MM.YYYY");
    },
    ftime: function(value) {
      return moment.unix(value).format("hh:mm");
    },
    filterCost: function(value) {
      return value <= 0 ? "Бесплатно" : value + "₽";
    },
    truncate: function(value, lengthWord) {
      if (!value) return "";
      value = value.toString();
      if (value.split(" ").length > lengthWord) {
        return (
          value
            .split(" ")
            .slice(0, lengthWord)
            .join(" ") + "..."
        );
      } else {
        return value;
      }
    }
  },
  methods: {
    getNoun(number, one, two, five) {
      let n = Math.abs(number);
      n %= 100;
      if (n >= 5 && n <= 20) {
        return five;
      }
      n %= 10;
      if (n === 1) {
        return one;
      }
      if (n >= 2 && n <= 4) {
        return two;
      }
      return five;
    }
  }
};
