<template>
  <div class="page" v-show="load">
    <div class="page__content">
      <div class="page__header">
        <breadcrumbs />
        <h1 class="page__title">{{ news.title }}</h1>
      </div>
      <div class="news-view">
        <div class="news-view__header">
          <div class="news-view__block-first">
            <div class="news-view__genre" v-if="getCategory">
              {{ getCategory }}
            </div>
            <div class="news-view__location">
              {{ news.published_at | formatDate }}
            </div>
          </div>
        </div>
      </div>
      <div class="news-view__body">
        <div class="news-view__image" v-if="news.title_picture.url">
          <img :src="news.title_picture.url" alt="" />
        </div>

        <div class="news-view__description">
          <div class="news-view__col-left" v-html="news.body"></div>
        </div>

        <div class="news-view__gallery" v-if="news.images.length">
          <image-gallery :images="news.images"></image-gallery>
        </div>
        <div class="news-list">
          <h2 class="news-list__title">Больше интересных новостей</h2>
          <div class="news-cards">
            <news-card
              v-for="newsItem in news.random_posts"
              :news="newsItem"
              :key="newsItem.id"
              :id="newsItem.id"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import mixin from "../mixin/mixin";
import ImageGallery from "../components/ImageGallery";
import NewsCard from "../components/Cards/NewsCard";

export default {
  name: "NewsViewPage",
  mixins: [mixin],
  components: { NewsCard, ImageGallery },
  data() {
    return {
      news: {
        title: "",
        title_picture: {
          url: ""
        },
        images: [],
        categories: []
      },
      load: false
    };
  },
  mounted() {
    this.loadData();
  },

  methods: {
    async loadData() {
      let load = this.$loading.show();

      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/post/view`);
      url.searchParams.set("id", this.$route.params.id);
      url.searchParams.set("expand", "random_posts");

      await axios
        .get(url)
        .then(response => {
          this.news = response.data;
          this.load = true;
          load.hide();
        })
        .catch(error => {
          if (error.response.status === 404) {
            this.$toasted.global.error("Новость не найдена");
            this.$router.push("/news");
          } else {
            this.$toasted.global.error(error.response.data.message);
          }
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          load.hide();
        });
    }
  },
  computed: {
    getCategory() {
      return this.news.categories.length > 0
        ? this.news.categories[0].title
        : "";
    }
  }
};
</script>
